import * as tslint from 'tslint';
import { TSLintTask, ITSLintTaskConfig } from 'gulp-core-build-typescript/lib/TSLintTask';
import { GulpTask } from 'gulp-core-build';

export interface ITSLintPartialConfig {
  rulesDirectory: string | string[];
  /* tslint:disable:no-any */
  rules: { [ruleName: string]: any };
  /* tslint:enable:no-any */
}

const tslintConfig: ITSLintPartialConfig = require('./tslint.json');

const rulesDirectory: string[] = tslint.getRulesDirectories([ './rules' ], __dirname);

export function initializeTslintTask(tslintTask: TSLintTask | GulpTask<{}>): void {
  const existingRulesDirectories: string[] | string =
    (tslintTask.taskConfig as ITSLintTaskConfig).rulesDirectory;

  if (existingRulesDirectories) {
    if (existingRulesDirectories instanceof Array) {
      rulesDirectory.push(...(existingRulesDirectories as string[]));
    } else {
      rulesDirectory.push(existingRulesDirectories as string);
    }
  }

  tslintTask.setConfig({
    lintConfig: tslintConfig,

    rulesDirectory: rulesDirectory
  });
}
